	const handleLoadOptions = (inputValue, callback) => {
		return fetch(GetContractsURL, {
		   	method: 'get',
		   	headers: {'Content-Type':'application/json'}
		}).then(response => response.json()
		).then((response: any) => {
			let searchResult = response.reduce ((result, item) => {
				const search = {
					label: item.name,
					value: item._id
				};

				result.push (search);
				
				return result;
			}, new Array());

			if (inputValue !== "") {
				searchResult = searchResult.filter(i =>
					i.label.toLowerCase().includes(inputValue.toLowerCase())
				);
			}

			callback (searchResult);
		}).catch(error => {
			console.log('getcontractlist api error: ', error);
		});
	};


	const convertNodeData = (data: any[]) => {
		let nodeIDMap: any[] = [];

		let nodes = [...data];

		nodes = nodes.filter ((item: any) => {
			if (item.level === 1 || 
				item.level === 2 || 
				(item.level > 2 && item.tree_path[2] === 'BASE')
			) {
				return true;
			} 
			return false;
		});

		nodes = nodes.map ((item: any, i: number) => {
			const oldID = item['_id'];
			let itemPlanType = 'BASE';

			if (item.level > 2 && item.tree_path[2] !== 'BASE') {
				itemPlanType = item.tree_path[2];
			}

			const newID = "NODE-" + itemPlanType + "-"+i.toString().padStart(3, '0');

			item['included'] = true;

			const IDSet = {
				id: oldID,
				newID: newID
			};

			nodeIDMap.push (IDSet);

			delete item['_id'];
			item['id'] = newID;

			return Object.assign({}, item);
		});

		nodes = nodes.map ((item: any) => {
			if (item['children'].length > 0) {
				item['children'] = item['children'].reduce ((result, id) => {
					const found = nodeIDMap.find ((set) => { return set.id === id; });

					if (found) {
						result.push(found.newID)
					}

					return result;
				}, new Array());
			}
			return item;
		});

		return nodes;
	}

